import React from 'react'
import { Link } from 'react-router-dom';


export default function Navbar(props) {
    return (
        <nav className={`navbar navbar-expand-lg navbar-${props.mode} bg-${props.mode}`}>
            <div className="container-fluid">
                <a className="navbar-brand" href="/"> {props.title} </a>
                <button className="navbar-toggler" type="button" data-bs-toggle="collapse"
                    data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false"
                    aria-label="Toggle navigation"><span className="navbar-toggler-icon"></span>
                </button>
                <div className="collapse navbar-collapse" id="navbarSupportedContent">
                    <ul className="navbar-nav me-auto mb-2 mb-lg-0">
                        <li className="nav-item">
                            <Link className="nav-link active" aria-current="page" to="/">{props.navItem1}</Link>
                        </li>
                        <li className="nav-item">
                            <Link className="nav-link" to="/about">{props.navItem2}</Link>
                        </li>
                        <li className="nav-item">
                            <Link to="/buttons" style={{ color: 'black' }} activeStyle={{ color: 'red' }}>Users</Link>
                        </li>

                    </ul>
                    <div className="form-check form-switch text-dark">Red
                        <input className="form-check-input bg-danger" type="checkbox" role="switch" aria-checked id="flexSwitchCheckDefault" onClick={props.redMode} />
                        <label className="form-check-label bg-dark" htmlFor="flexSwitchCheckDefault"></label>
                    </div>
                    <form className="d-flex" role="search">
                        <input className="form-control me-2" type="search" placeholder="Search" aria-label="Search" />
                        <button className="btn btn-info" type="submit">
                            Search
                        </button>
                    </form>
                    <div className={`form-check form-switch text-${props.mode === 'light' ? 'dark' : 'light'}`}>
                        <input className="form-check-input" type="checkbox" role="switch" aria-checked id="flexSwitchCheckDefault" onClick={props.toggleMode} />
                        <label className="form-check-label" htmlFor="flexSwitchCheckDefault">{props.label}</label>
                    </div>
                </div>
            </div>
        </nav>
    )
}
